export default {
	components: {
		// uniLoadMore
	},
	data() {
		return {
			course_ss:[],
		}
	},
	onShow() {
		this.course_ss = []
		var coursefiles = uni.getStorageSync('course_file')
		var teacher = uni.getStorageSync('teacher')
		var course_file = []
		for(var i=0;i<coursefiles.length;i++){
			if(teacher.number==coursefiles[i].teacher_id){
				this.course_ss.push(coursefiles[i])
			}
		}
		console.log("文件:",this.course_ss)
	},
	methods: {
		downFile(url){
			uni.downloadFile({
			        url: 'http://qguasf.natappfree.cc/media/'+url,
			        header: {},
			        success: function(res) {
			            var filePath = res.tempFilePath;
			            console.log(filePath);
			            uni.openDocument({
			                filePath: filePath,
			                success: function(res) {
			                    console.log('打开文档成功')
			                },
			                fail: function(res) {
			                    console.log(res);
			                },
			                complete: function(res) {
			                    console.log(res);
			                }
			            })
			        },
			        fail: function(res) {
			            console.log(url);
			            console.log('文件下载失败');
			        },
			        complete: function(res) {},
			    })
		}
	}
}
